<template>
  <div class="main-channel-sub-box">
    <ul class="curr-channel-sub-list">
      <li class="sub-list-single" v-for="(subItem,subIndex) in currMChannelList"
          v-if="Array.isArray(currSubChannelList[subItem.channelCode])&&currSubChannelList[subItem.channelCode].length!=0">
        <div class="sub-channel-content clearfix">
          <span class="title fl">{{subItem.channelName | crudeoil}}</span>
          <em class="fr more" @click="toArticleList(subItem.channelCode)">MORE</em>
        </div>
        <ul class="sub-channel-list">
          <li class="sub-channel-list-item" v-for="(item,index) in currSubChannelList[subItem.channelCode]">
            <div class="clearfix">
              <h3 class="fl sub-channel-title" @click="seeDetails(item.uuid)">{{item.title}}</h3>
              <span class="date fr">{{item.release_date | ddMMMyyyy}}</span>
            </div>
            <div class="infor-brief">
              {{item.tex}}
            </div>
          </li>
        </ul>
      </li>
    </ul>
    <sub-channel-news-details :visible.sync='detailsVisible' :detailsData = "detailsData"></sub-channel-news-details>
  </div>
</template>
<script>
  import SubChannelNewsDetails from '@/modules/SubChannelNewsDetails.vue'
  import {channelSubList,listDatasd,listDataParticulars} from '@/api/info'
  export default {
    name: "aboutcrudeoil",
    components:{
      SubChannelNewsDetails
    },
    data(){
      return {
        state:this.$store,
        detailsVisible:false,
        currMChannelList:[],      //主频道列表
        currSubChannelList:{},     //子频道列表
        detailsData:{}
      }
    },
    watch:{},
    mounted(){
      this.getSubData()
    },
    methods:{
      seeDetails(id){

        listDataParticulars({infoUuid:id}).then((res)=>{
          if(res.status==0){
            this.detailsVisible = true
            this.detailsData = res.datas
          }
        })

      },
      getSubData(){
        channelSubList({channelMDesc:'CPY',channelMName:'ABOUT CRUDEOIL'}).then((res)=>{
            // console.log(res)
          this.currMChannelList = res.datas
          this.getSubListData()
        })
      },
      getSubListData(){
        let a = this.currMChannelList.map((item)=>{
          let b = {}
          b.channelCode = item['channelCode']
          b.channelMDesc = item['channelMDesc']
          return b
        })
        a.forEach((item)=>{
          listDatasd(item).then((res)=>{
            this.$set(this.currSubChannelList,item['channelCode'],res.datas.infoList.slice(0,3))
          })
        })
      },
      toArticleList(name){
        let parse = name.toLowerCase().split(' ').join('_')
        this.$router.push(`/about_crudeoil/${parse}`)
      }
    }
  }
</script>

<style scoped>
  .title{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #F36523;
    margin-bottom: 20px;
    max-width: 731px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .more{
    font-family: ArialMT;
    font-size: 13px;
    color: #333333;
    cursor: pointer;
  }
  .date{
    font-family: ArialMT;
    font-size: 14px;
    color: #999999;
  }

  .curr-channel-sub-list{
    width: 840px;
  }
  .sub-list-single{
    margin-top: 38px;
    padding-bottom: 38px;
    border-bottom: 1px solid #ECECEC;
  }
  .sub-channel-list .sub-channel-list-single{
    margin-top: 20px;
  }
  .sub-channel-list .sub-channel-list-single:first-child{
    margin-top: 0;
  }
  .sub-channel-list li{
    margin-top: 20px;
  }
  .sub-channel-title{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #333333;
    max-width: 731px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .sub-channel-title:hover{
    text-decoration: underline;
    cursor: pointer;
  }
  .infor-brief{
    height:60px;
    line-height: 30px;
    overflow: hidden;
    font-family: ArialMT;
    font-size: 13px;
    color: #333;
    margin-top: 10px;
  }
</style>





